/* For screens smaller than 768px */
@media (max-width: 768px) {
  .main-content {
    padding: 0 20px; /* Add some padding to the sides */
  }

  .dashboard {
    margin: 2rem 0; /* Remove the right margin */
  }

  .dashboard-sidebar {
    flex-direction: column; /* Stack the cards vertically */
    gap: 10px; /* Decrease the gap between cards */
  }
  .dashboard-sidebar ul {
    display: flex;
    flex-wrap: wrap; /* Allow items to wrap to the next line */
    justify-content: center; /* Center items horizontally */
    padding: 0; /* Remove default padding */
    margin: 0; /* Remove default margin */
    list-style: none; /* Remove default list styles */
  }

  .dashboard-sidebar ul li {
    margin: 5px; /* Adjust margin between items */
  }

  .messages-container {
    width: 100%; /* Make the messages container full width */
    padding: 1rem; /* Adjust padding */
  }

  .table-container {
    width: 100%; /* Make the table container full width */
    padding: 1rem; /* Adjust padding */
  }
  table {
    display: block;
  }
}

/* For screens smaller than 480px */
@media (max-width: 480px) {
  .cards-dashboard {
    flex-direction: column; /* Stack the cards vertically */
  }
}

/* Styles for screens above 768px and below 1400px */
@media (min-width: 769px) and (max-width: 1399px) {
  .dashboard {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .content {
    width: 50%;
  }

  .card-dashboard {
    flex: 0 1; /* Two cards per row with margin */
    margin-bottom: 20px;
  }

  .cards-dashboard {
    flex-direction: column;
    width: 50%;
  }

  .messages-container {
    width: 50%;
    margin: 0; /* Half the width with margin */
  }

  .table-container {
    width: 50%; /* Half the width with margin */
  }

  table {
    display: block;
    width: 100%;
  }
}

@media (max-width: 768px) {
